<template>
  <div>
    <Navbar />
    <main class="main d-flex">
      <ValidationObserver v-slot="{ handleSubmit }" class="form-signin">
        <form @submit.prevent="handleSubmit(signin)">
          <h1 class="h2 mb-3 text-center">後台管理頁面</h1>
          <ValidationProvider
            name="帳號"
            rules="required|email"
            v-slot="{ errors }"
            class="d-block"
          >
            <label for="inputEmail" class="font-weight-bold required"
              >帳號*</label
            >
            <input
              v-model="user.username"
              id="inputEmail"
              type="text"
              class="form-control"
              placeholder="輸入帳號"
            />
            <small class="form-text text-danger text-left">{{
              errors[0]
            }}</small>
          </ValidationProvider>
          <ValidationProvider
            name="密碼"
            rules="required"
            v-slot="{ errors }"
            class="d-block mt-2"
          >
            <label for="inputPassword" class="font-weight-bold required"
              >密碼*</label
            >
            <input
              v-model="user.password"
              id="inputPassword"
              type="password"
              class="form-control"
              placeholder="輸入密碼"
            />
            <small class="form-text text-danger text-left">{{
              errors[0]
            }}</small>
          </ValidationProvider>
          <button class="btn btn-lg btn-warning btn-block mt-3" type="submit">
            登入帳號
          </button>
          <div class="text-right mt-3">
            <router-link class="text-success" :to="{ name: 'Shop' }"
              >返回首頁</router-link
            >
          </div>
        </form>
      </ValidationObserver>
    </main>
  </div>
</template>

<script>
import Navbar from '@/components/Navbar.vue';

export default {
  name: 'Login',
  components: {
    Navbar,
  },
  data() {
    return {
      user: {
        username: '',
        password: '',
      },
    };
  },
  methods: {
    signin() {
      const url = `${process.env.VUE_APP_APIPATH}/admin/signin`;
      const vm = this;
      vm.axios.post(url, vm.user).then((response) => {
        if (response.data.success) {
          const { token, expired } = response.data;
          document.cookie = `hexToken=${token};expires=${new Date(expired)};`;
          vm.$router.push({ name: 'DashboardProducts' });
        }
      });
    },
  },
};
</script>
